@use "../variables.scss" as*;

.link-logo {
  display: block;
}

.link-logo__img {
  display: block;
  transition: opacity 0.3s;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet) {
    width: 114px;
    height: 46px;
  }

  @media (min-width: $desktop) {
    width: 149px;
    height: 72px;
  }
}
